.header__logo {
  margin: 0;
  padding: 0;
  object-fit: cover;
  width: 104px;
  height: 24.4px;
  display: block;
}

@media (min-width: 426px) and (max-width: 768px) {
  .header__logo {
    width: 123px;
    height: 28px;
  }
}

@media screen and (min-width: 769px) {
  .header__logo {
    width: 142px;
    height: 33px;
  }
}